.popup-img__caption {
  font-weight: normal;
  font-size: 12px;
  line-height: 15px;
  margin-top: 10px;
}

@media screen and (max-width: 920px) {
  .popup-img__caption {
    max-width: 180px;
  }
}

@media screen and (min-width: 920px) {
  .popup-img__caption {
    max-width: 291px;
  }
}